import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { AreaService } from 'src/app/service/area.service';
import { PutAwayComponent } from './put-away.component';
import { PutAwayService } from 'src/app/service/put-away.service';

const routes: Routes = [
  {
    path: '',
    component: PutAwayComponent,
    data: { title: 'product_register_list' },
  },
  {
    path: 'add',
    loadChildren: () => import('./add-put-away/add-put-away.module').then(m => m.AddPutAwayModule),
    data: { title: 'add_product_register' },
  },
  {
    path: 'edit',
    loadChildren: () => import('./add-put-away/add-put-away.module').then(m => m.AddPutAwayModule),
    data: { title: 'edit_product_register' },
  }

]


@NgModule({
  declarations: [PutAwayComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    PaginationModule,
    SharedModule,
  ], providers: [PutAwayService]
})

export class PutAwayModule { }
