import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { TextField, ActionField, DateField } from 'src/app/common-module/table-list/list-field-type';
import { PutAwayService } from 'src/app/service/put-away.service';

@Component({
  selector: 'app-put-away',
  templateUrl: './put-away.component.html',
})
export class PutAwayComponent implements OnInit {

  PageTitle = "Manage Putaway";

  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;
  showPagination: boolean = false;
  //For Dynamic List
  headerData: Array<any> = [];

  constructor(
    private paginationService: PaginationService,
    private putawayService: PutAwayService,
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['self'];
    // });
    this.loadform = false;
    this.setHeaderData();
    this.getObjects();
  }
  setHeaderData() {
    this.headerData.push(
      new TextField({
        label: "Order Number",
        key: "order.po_no",
      }),
      new TextField({
        label: "Transfer Order No",
        key: "transfer_order_no",
      }),
      new TextField({
        label: "SKU",
        key: "sku_no.sku_no",
      }),
      new TextField({
        label: "Bin Code",
        key: "put_away.bin_code",
      }),
      new TextField({
        label: "Bin Tag Id",
        key: "put_away.bin_tag_id",
      }),
      new TextField({
        label: "Putaway Qty",
        key: "put_away.put_away_qty",
      }),
      // new DateField({
      //   label: "Putaway Date",
      //   key: "created_date",
      // }) ,
    );
    let canEdit = true;
    let canDelete = true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/inbound/put-away/edit/',
        id: 'transfer_order_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'transfer-order/',
        id: 'transfer_order_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.putawayService.getPutAwayList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {

        this.objectArray = response.data.list ? response.data.list : [];
        this.showPagination = true;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }
  onReloadEvent() {
    this.getObjects();
  }
}




